import React from "react";
import styled from "styled-components";
import { photos } from "../constants";
import { MediaQuaeries } from "../styled/component";
import { LazyLoadImage } from 'react-lazy-load-image-component';

const GaleryContainer = styled.div`
    display: grid;
    grid-template-columns:repeat(4,1fr);

    @media ${MediaQuaeries.media.medium960} {
        align-items: center;
        grid-template-columns:1fr;
        margin: 55px 0;
       
    }
   

    @media ${MediaQuaeries.media.smal450} {
        grid-template-columns:1fr;
        margin: 55px 0;
    }
   
`
const ImjGalery = styled.div`
   
    img {
        width: 100%;
        height: 100%;
        display: block;
        @media ${MediaQuaeries.media.medium960} {
            margin: auto;
        width: 40%;
        height: 40%;
        display: block;
    }
    @media ${MediaQuaeries.media.smal450} {
            margin: auto;
        width: 100%;
        height: 100%;
        display: block;
    }
    }
`

export const Galery: React.FC = () => {
    return (
        <GaleryContainer>
            {photos.map((item: { src: string }, index: number) => {
                return (
                    <ImjGalery key={index}>
                        <LazyLoadImage src={item.src} alt="" />
                    </ImjGalery>
                );
            })}
        </GaleryContainer>
    )
}