import React, { useEffect, useState } from "react";
import { Equalizer, IMusik, PlayBlock, TrackBlockItem, TrackName } from "../MusikPage";
import Rectangle from '../../assets/img/Rectangle.jpg'
import { PlayIcon, StopIcon } from "../../assets/img/svg";

interface IMusikMpProps {
    item: IMusik;
}

export const MusikMp: React.FC<IMusikMpProps> = ({ item }) => {
    const [audio] = useState<HTMLAudioElement>(new Audio(item.track));
    const [playing, setPlaying] = useState<boolean>(false);
    useEffect(() => {
        playing ? audio.play().then(_ => { })
            : audio.pause()
    },
        [audio, playing]
    );

    return (
        <TrackBlockItem>
            <PlayBlock>
                <button onClick={() => setPlaying(!playing)}>
                    {!playing ? (<PlayIcon />) : (<StopIcon />)}
                </button>
                <TrackName>{item.trackName}</TrackName>
            </PlayBlock>
            <Equalizer>
                <img src={Rectangle} alt="" />
            </Equalizer>
        </TrackBlockItem>
    )
}