import React from "react";
import styled from "styled-components";
import ImgAva from '../assets/img/Rectangle45.jpg'
import { fontStyles, TitleText, Gradient, GradientFon, MediaQuaeries } from "../styled/component";

const AboutMyBlock = styled.div`
    position: relative;
    display: flex;
    justify-content: revert;
    padding: 10px;
    background: white;
    &::before{
        width: 100%;
        height: 100%;
        content: "";
        position: absolute;
        top: 0;
        border-radius: 10px;
        background: linear-gradient(to right top, #7F0035, #0A007F);
        z-index: -1;
    }
    @media ${MediaQuaeries.media.medium960} {
        flex-direction: column;

    }
    @media  ${MediaQuaeries.media.smal450} {
        flex-direction: column;
        justify-content: center;
    }
    img{
        max-width: 620px;
        height: fit-content;
        margin: 0 10px;
        @media  ${MediaQuaeries.media.medium960} {
            margin:auto;
            width: 400px;
    }
        @media  ${MediaQuaeries.media.smal450} {
            margin:auto;
            width: 300px;
    }
    }
`
const TextAboutMy = styled.div`
    padding: 0 10px;
    overflow: hidden;
  ${fontStyles}
  @media  ${MediaQuaeries.media.smal450} {
        padding: 0 ;}
`
const Title = styled(TitleText)`
    padding: 0 0 0 90px;
    @media  ${MediaQuaeries.media.smal450} {
        font-size: 22px;
        text-align: start;
        padding: 0 15px;
      
      
    }
`


export const AboutMy = () => {
    return (
        <Gradient>
            <GradientFon>
            <Title >   </Title>
            <AboutMyBlock>
                <img src={ImgAva} alt="" />
                <TextAboutMy>
                    <span>
                        Dj Alyssa.   -  .
                               ,
                                2008 .
                          ,
                                .
                          ,    .
                        <br />
                                  ,
                             .      ,
                              .    ,
                             .
                         ,    ,   .
                        <p />
                                  .
                           
                             .
                        <p />
                            ,     .
                                   .
                        <p />
                            !
                    </span>

                </TextAboutMy>
            </AboutMyBlock>
            </GradientFon>
        </Gradient>
    )
}