import React from "react";
import styled from "styled-components";
import { Container,  MediaQuaeries } from "../styled/component";
import img from '../assets/PNG/Group5.png'
import img1 from '../assets/PNG/Group55.png'
import vk from '../assets/social/vk.png'
import instagram from '../assets/social/in.png'
import tg from '../assets/social/tg.png'

const HomePageBlock = styled.div`
width: 100%;
height: 100vh;
background-image: url(${img});
background-size: cover;

@media ${MediaQuaeries.media.medium960} {
    margin-top: 50px ;
    }


@media ${MediaQuaeries.media.smal450} {
    margin-top: 40px;
    background-image: url(${img1});
    background-repeat: no-repeat;
    background-size: cover;
    max-width:465px;
    }
`
const TitleHomePage = styled.div`
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
color: white;
font-family: 'Inter';
font-weight: 100;
font-size: 150px;
line-height: 182px;
text-align: center;
text-transform: uppercase;
text-shadow: 0px 4px 4px #000000;

@media ${MediaQuaeries.media.medium960} {
        top: 70%;
        font-size: 70px;
        line-height: 90px;
    }
@media ${MediaQuaeries.media.large760} {
        font-size: 50px;
        line-height: 90px;
    }

@media ${MediaQuaeries.media.smal450} {
        top: 50%;
        font-size: 74px;
        line-height: 90px;

    }

`
const FooterBlock = styled.footer`
    position: absolute;
    bottom:20px;
    left: 50%;
    transform: translate( -50%) ;
    display: flex;
    justify-content: center;
    margin: auto;
    text-align: center;

    @media ${MediaQuaeries.media.medium960} {
        bottom:0px;
    }
    img{
        margin: 20px;
       border: 2px solid white;
       border-radius: 5px;
       width: 35px;
       height: 35px;
       opacity: 0.8;
       :hover{
        opacity: 1;
        border: 2px solid red;

       }
    }
    
`
export const HomePage = () => {
    return (
        <Container>
            <HomePageBlock>
                <TitleHomePage>DJ ALYSSA</TitleHomePage>
                <FooterBlock>
                    <a href="https://vk.com/dj_alyssa" target="_blank"> <img src={vk} alt="" /></a>
                    <a href="http://instagram.com/deejay_alyssa" target="_blank"> <img src={instagram} alt="" /></a>
                    <a href="https://t.me/deejay_alyssa" target="_blank"> <img src={tg} alt="" /></a>
                </FooterBlock>
            </HomePageBlock>


        </Container>
    )
}