import React from "react";
import styled from "styled-components";
import { Title, Gradient, GradientFon, MediaQuaeries } from "../styled/component";
import Save from '../assets/img/save.png'
import { musik, videoTrack } from "../constants";
import { MusikMp } from "./MusikPlay/MusikMp";
import ReactPlayer from "react-player";


const ContaunerTrackBlock = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;

    @media ${MediaQuaeries.media.medium960} {
        padding: 0 25px;
    }
    @media ${MediaQuaeries.media.large760} {
        padding: 0;
    }
    @media ${MediaQuaeries.media.large760} {
        padding: 0;
        margin:  0;
    }
    @media ${MediaQuaeries.media.smal450} {
        padding: 0;
    }

    `

const TrackBlock = styled.div`
    position: relative;
    display: flex;
    padding: 5px 30px;
    max-width:1150px ;
    width: 100%;
    background: #B0B0B0;
    margin-bottom: 15px;
    border-radius: 3px;

    @media ${MediaQuaeries.media.medium960} {
        padding: 5px 15px;
    }

    @media ${MediaQuaeries.media.large760} {
        margin: 0px 0px 10px 0px; 
        width: 90%;
        padding: 5px 15px;

    }

    @media ${MediaQuaeries.media.smal450} {
        width: 100%;
        margin: 0 5px 10px 5px;
    }
`
export const TrackBlockItem = styled.div`
    margin-left: 35px;
    width: 100%;
    @media ${MediaQuaeries.media.large760} {
        margin-left: 0px;
    }
    @media ${MediaQuaeries.media.smal450} {
        margin-left: 15px;
    }

`
const Ava = styled.div`
    display: flex;
    align-items: center;
img{
    width: 140px;
}

@media ${MediaQuaeries.media.large760} {
    display: none;
    }
`
export const PlayBlock = styled.div`
display: flex;
padding: 5px 10px 10px;
max-width: 300px;
align-items: center;
font-size: 22px;
button{
    padding: 0 20px 0 0;
    background: transparent;
    border: none;
}
    img{
        width: 55px;
        margin-right: 20px;
    }
    @media ${MediaQuaeries.media.large760} {
        padding: 5px 0px 10px;
    }
`
const RectangleItem = styled.div`
    position: absolute;
    top: 20px;
    right: 50px;
    img {
        margin: 5px;
        background: transparent;
        border: none;
        opacity: 0.7;
        cursor: pointer;
        :hover{
            opacity: 1;
        }
    }
img{
    width: 27px;
}
@media ${MediaQuaeries.media.smal450} {
    position: absolute;
    top: 90px;
    right: 25px;
    }
    img{
    width: 25px;
}
`

const VideoBlock = styled.div`
    display: flex;
    justify-content: center;
    margin: 0px auto 20px;

    @media ${MediaQuaeries.media.medium960} {
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 10px;
    }
        `

const VideoItem = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    margin: 0 10px;
    padding: 50px 0;
    height: 100%;
    border-radius: 10px;
    background: rgb(0, 0, 0);
    @media ${MediaQuaeries.media.medium960} {
        flex-direction: column;
        width: 100%;
        padding: 30px 0;
        margin: 5px 0;
    }
`
const YoutubeIcon = styled.div`
`
const FooterTitle = styled.span`
    bottom: 5px;
    left: 20px;
    text-align: center;
    margin: 10px 0 0 0 ;
    font-size: 14px;
    color: white;
`
export const Equalizer = styled.div`
    img{
        width: 780px;
    }
    @media ${MediaQuaeries.media.medium960} {
        img{
        width: 320px;
    }
        }
    @media ${MediaQuaeries.media.large760} {
        img{
        width: 320px;
    }
        }
    @media ${MediaQuaeries.media.smal450} {
        img{
            width: 245px;
            height:52px;
        }
        
    }
`
const ReactPlayerStyled = styled(ReactPlayer)`
   width: 390px !important;
   height: auto !important;

   @media ${MediaQuaeries.media.medium960} {
    height: auto !important;
    width: 340px !important;
    }
   @media ${MediaQuaeries.media.large760} {
    width: 340px !important;
    height: auto !important;
    }
    @media ${MediaQuaeries.media.smal450} {
        height: auto !important;
    }
`
export const TrackName = styled.span`
padding: 0 0 0 5px ;
font-size: 22px;
text-transform: uppercase;
`
export interface IMusik {
    trackImege: string,
    track: string,
    trackName: string,
}
export interface Ivideo {
    nameVideo: string,
    poster: string,
    url: string,
}

export const MusikPage = () => {

    return (
        <Gradient >
            <GradientFon>
                <Title></Title>
                <ContaunerTrackBlock >
                    {musik.map((item: IMusik, index: number) => {
                        return (<TrackBlock key={index}>
                            <Ava>
                                <img src={item.trackImege} alt="" />
                            </Ava>
                            <MusikMp
                                item={item}
                            />
                            <RectangleItem>
                                <a href={item.track} download>
                                    <img src={Save} alt="" />
                                </a>
                            </RectangleItem>
                        </TrackBlock>)
                    })}
                </ContaunerTrackBlock>
                <VideoBlock>
                    {
                        videoTrack.map((item: Ivideo, index: number) => {
                            return (<VideoItem key={index}>
                                <ReactPlayerStyled
                                    url={item.url}
                                    controls
                                />
                                <YoutubeIcon>
                                </YoutubeIcon>
                                <FooterTitle>
                                    {item.nameVideo}
                                </FooterTitle>
                            </VideoItem>
                            )
                        })
                    }

                </VideoBlock>
            </GradientFon>

        </Gradient>
    )
}