import styled, { css } from 'styled-components';



export const Container = styled.div`
`

export const MediaQuaeries = {
    media: {
        medium960: '(max-width: 960px)',
        large760: '(max-width: 740px)',
        smal450: '(max-width:450px)'
    }
}


export const ContainerBlock = styled.div`
margin: 28px 98px 0 0;
max-width: 1279px;
width: 100%;
height: 1002px;
border: 3px solid red;
@media ${MediaQuaeries.media.smal450} {
    margin: 10px;
    height: auto;
    float:none;
    width: auto;
}
`

export const TitleText = styled.h2`
text-transform: uppercase;
font-style: normal;
font-weight: 400;
font-size: 36px;
line-height: 1.2;
color: #820046;
@media ${MediaQuaeries.media.smal450} {
    margin: 10px 0 0;
    font-size: 22px;
        }
`
export const Title = styled(TitleText)`
    text-align: inherit;
    padding-left: 40px;
    @media ${MediaQuaeries.media.medium960} {
        text-align: center;
        padding-left: 0;
    }
    @media ${MediaQuaeries.media.large760} {
        padding: 0 0 0 10px;
    }

    @media ${MediaQuaeries.media.smal450} {
        font-size: 22px;
        text-align: start;
        padding: 0 0px;
    }
`
export const fontStyles = css`
font-style: normal;
font-weight: 400;
font-size: 22px;
line-height: 27px;
@media ${MediaQuaeries.media.smal450}{
    font-size: 14px;
    }
`
export const Gradient = styled.div`
    margin: 30px 10px 25px 58px;
    background:linear-gradient(
        45deg, #7F0035 , #0A007F
    );
    border-radius:10px;
    @media ${MediaQuaeries.media.medium960} {
        margin: 80px 20px 20px 20px;
    }
    @media ${MediaQuaeries.media.smal450} {
        margin: 65px 10px 10px 10px;
    }
`

export const GradientFon = styled.div`
margin: 4px;
height:100%;
background: white;
border-radius: 10px;
@media ${MediaQuaeries.media.smal450} {
        padding: 0 5px;
    }
`

export const baseTheme = {
    colors: {
        primary: '#66007F',
        secondary: '#ffffff',
        success: '#ff0000',
        danger: 'f90202 ',

    }
}