import React from "react";
import { Link } from "react-router-dom";
import styled from "styled-components";
import { menu } from "../constants";
import { MediaQuaeries } from "../styled/component";

const ContainerBlock = styled.div`
position: fixed;
margin: 25px 0 0 25px;
z-index: 10;
max-width: 265px;
background: linear-gradient(#7F0035E5 , #0A007FE5  );
color: white;
@media  ${MediaQuaeries.media.medium960} {
    display: none;
    margin: 20px 0 0 15px;
    }
@media  ${MediaQuaeries.media.large760} {
    margin: 0px 0 0 15px;
    }
@media  ${MediaQuaeries.media.smal450} {
        display: none;
    }
`


const SideBarMeny = styled.ul`
display: flex;
flex-direction: column;
justify-content: center;
font-size: 22px;
font-weight: 200;
text-shadow: 0px 4px 4px #000000;
align-items: center;
font-style: normal;
line-height: 26px;
margin: 0;
padding: 0;
a{
    color: white;
    padding: 10px 0 15px;
    text-decoration: none;
    :active{
        color:#B87900 ;
    }
}

`
const SideBarHeder = styled.span`
    text-align: center;
    width: 100%;
    font-size: 36px;
    background: #66007F ;
    padding: 19px ;
`

export const SideBar: React.FC = () => {
    
    return (
        <ContainerBlock>
            <SideBarMeny>
                <SideBarHeder>
                    DJ Alyssa
                </SideBarHeder>
                {menu.map((item: { link: string, nameItem: string }, index: number) => {
                    return <Link to={`/${item.link}`} key={index}>{item.nameItem}</Link>
                })}
            </SideBarMeny>
        </ContainerBlock >
    )
}
